@font-face {
  font-family: "ProximaBold";
  src: url("../fonts/ProximaSoft-Bold.ttf");
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  color: #4E4E4E;
}

html {
  scroll-behavior: smooth;
  background-color: #F2F2F2;
}

.container {
  width: 1120px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}